"use client";
import ExpensesBreakdown from "@/components/dashboard/ExpensesBreakdown";
import Goals from "@/components/dashboard/Goals";
import Sidebar from "@/components/sidebar/Sidebar";
import {
  ComposedChart,
  Area,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  CartesianGrid,
} from "recharts";

const data = [
  { date: "May 01", thisMonth: 4000, lastMonth: 2400 },
  { date: "May 05", thisMonth: 3200, lastMonth: 2800 },
  { date: "May 10", thisMonth: 2800, lastMonth: 3000 },
  { date: "May 15", thisMonth: 3600, lastMonth: 2600 },
  { date: "May 20", thisMonth: 3900, lastMonth: 2700 },
  { date: "May 25", thisMonth: 3700, lastMonth: 2500 },
  { date: "May 30", thisMonth: 4200, lastMonth: 2600 },
];

export default function SavingSummary() {
  return (
    <div className="p-4 md:p-5 flex flex-col lg:flex-row gap-4">
      {/* Sidebar */}
      <div className="w-0 h-80">
        <Sidebar />
      </div>

      {/* Right Section */}
      <div className="flex-1 flex flex-col gap-4">
        {/* Top Row: Goals + Graph */}
        <div className="flex flex-col lg:flex-row gap-4">
          {/* Goals */}
          <div className="bg-white rounded-2xl shadow-md p-4 w-full lg:w-72 h-96">
            <Goals />
          </div>

          {/* Graph */}
          <div className="bg-white rounded-2xl shadow-md p-4 w-full flex-1 h-80">
            {/* Header */}
            <div className="flex items-center justify-between flex-wrap gap-2">
              <h2 className="text-base sm:text-lg font-semibold">
                Saving Summary
              </h2>
              <div className="text-gray-500 text-xs sm:text-sm flex items-center gap-1">
                Mar 2022 <span>â–¼</span>
              </div>
            </div>

            {/* Graph */}
            <div className="mt-4 h-56 sm:h-64 lg:h-60 relative">
              {/* Legend */}
              <div className="absolute right-2 top-2 z-10 flex flex-wrap items-center gap-3 text-xs sm:text-sm">
                <span className="flex items-center gap-2">
                  <span className="inline-block w-3 sm:w-4 h-1.5 rounded bg-teal-600" />
                  This month
                </span>
                <span className="flex items-center gap-2 text-gray-500">
                  <span className="inline-block w-3 sm:w-4 h-1.5 rounded bg-gray-300" />
                  Last month
                </span>
              </div>

              <ResponsiveContainer width="100%" height="100%">
                <ComposedChart
                  data={data}
                  margin={{ top: 30, right: 20, left: 0, bottom: 0 }}
                >
                  <defs>
                    <linearGradient id="tealFill" x1="0" y1="0" x2="0" y2="1">
                      <stop
                        offset="0%"
                        stopColor="#0f766e"
                        stopOpacity="0.6"
                      />
                      <stop
                        offset="70%"
                        stopColor="#0f766e"
                        stopOpacity="0.25"
                      />
                      <stop offset="100%" stopColor="#0f766e" stopOpacity="0" />
                    </linearGradient>
                  </defs>

                  <CartesianGrid
                    stroke="#e5e7eb"
                    strokeDasharray="3 3"
                    vertical={false}
                  />
                  <XAxis
                    dataKey="date"
                    tickLine={false}
                    axisLine={false}
                    fontSize={10}
                  />
                  <YAxis
                    domain={[0, "dataMax + 1000"]}
                    tickCount={5}
                    allowDecimals={false}
                    tickLine={false}
                    axisLine={false}
                    tickFormatter={(v) => `$${v}`}
                    fontSize={10}
                  />
                  <Tooltip
                    formatter={(v) => [`$${v}`, ""]}
                    contentStyle={{
                      borderRadius: 10,
                      border: "none",
                      boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
                    }}
                  />

                  {/* THIS MONTH */}
                  <Area
                    type="monotone"
                    dataKey="thisMonth"
                    stroke="#0f766e"
                    strokeWidth={2}
                    fill="url(#tealFill)"
                    dot={false}
                  />

                  {/* LAST MONTH */}
                  <Line
                    type="monotone"
                    dataKey="lastMonth"
                    stroke="#94a3b8"
                    strokeWidth={2}
                    strokeDasharray="5 6"
                    dot={false}
                  />
                </ComposedChart>
              </ResponsiveContainer>
            </div>
          </div>
        </div>

        {/* Bottom Row: Expenses Breakdown */}
        <div className="bg-white rounded-2xl shadow-md p-4 w-full">
          <ExpensesBreakdown />
        </div>
      </div>
    </div>
  );
}
